@import "../../node_modules/normalize.css/normalize.css";
@font-face {
  font-family: 'Anton';
  font-weight: 400;
  font-style: normal;
  src: url(../fonts/anton-regular-webfont.woff) format('woff');
  src: url(../fonts/anton-regular-webfont.woff2) format('woff2');
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
ul,
li,
p {
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
}
body {
  font-family: 'Anton', sans-serif;
}
@keyframes bg-main {
  0% {
    background-color: #2f2682;
  }
  50% {
    background-color: #211a5b;
  }
  100% {
    background-color: #2f2682;
  }
}
.index {
  background-color: #2f2682;
  animation-name: bg-main;
  animation-duration: 20s;
  animation-iteration-count: infinite;
}
.hover-headline {
  position: fixed;
  bottom: 1vh;
  left: calc(50% - 75px);
  z-index: 2;
  width: 150px;
  margin: 0 auto;
  text-align: center;
  color: white;
  text-transform: uppercase;
  font-size: 2rem;
  line-height: normal;
}
@media (max-width: 992px) {
  .hover-headline {
    display: none;
  }
}
.page-main {
  padding-top: calc(50vh - 170px);
}
.page-main__skills,
.page-main__projects,
.page-main__contacts {
  margin-bottom: 50px;
}
.page-main__skills .hover-headline,
.page-main__projects .hover-headline,
.page-main__contacts .hover-headline {
  opacity: 0;
  transition: 1s;
}
.page-main__skills:hover .hover-headline,
.page-main__projects:hover .hover-headline,
.page-main__contacts:hover .hover-headline {
  opacity: 1;
  transition: 2s;
}
@media (max-width: 992px) {
  .page-main {
    padding-top: calc(50vh - 250px);
  }
}
@media (max-width: 768px) {
  .page-main {
    padding-top: 50px;
  }
}
.skills {
  font-size: 3.8rem;
}
.skills__list {
  margin-left: -10px;
}
.skills__item {
  margin-left: 10px;
  color: white;
  text-transform: uppercase;
  line-height: 3.5rem;
  text-align: center;
}
.skills__item--avocode {
  font-size: 0.8em;
}
.skills__item--bem {
  font-size: 0.8em;
}
.skills__item--bootstrap {
  font-size: 0.5em;
}
.skills__item--css {
  font-size: 0.9em;
}
.skills__item--git {
  font-size: 0.5em;
}
.skills__item--gulp {
  font-size: 0.7em;
}
.skills__item--html {
  font-size: 0.9em;
}
.skills__item--js {
  font-size: 0.3em;
}
.skills__item--jquery {
  font-size: 0.3em;
}
.skills__item--less {
  font-size: 0.8em;
}
.skills__item--photoshop {
  font-size: 0.7em;
}
.skills__item--pp {
  font-size: 0.8em;
}
.skills__item--sass {
  font-size: 0.8em;
}
.skills__item--vscode {
  font-size: 0.8em;
}
@media (min-width: 1200px) {
  .skills {
    font-size: 4rem;
  }
  .skills__item {
    line-height: 3.8rem;
  }
}
@media (max-width: 992px) {
  .skills {
    font-size: 3rem;
  }
  .skills__item {
    line-height: 2.5rem;
  }
}
.projects__item {
  position: relative;
  height: 100px;
  width: 100%;
  margin-bottom: 20px;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  transition: 2s;
}
.projects__item:last-of-type {
  margin-bottom: 0;
}
.projects__item:hover {
  box-shadow: 0 0 20px 0px white;
  transition: 1s;
}
.projects__title {
  width: 100%;
  padding-top: 4px;
  text-align: center;
  text-transform: uppercase;
  color: white;
  line-height: normal;
  background-color: rgba(0, 0, 0, 0.5);
}
.projects__link {
  position: absolute;
  top: 0;
  display: block;
  height: 100%;
  width: 100%;
}
.contacts__list {
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
}
.contacts__item {
  margin-bottom: 10px;
  color: white;
  font-size: 30px;
}
.contacts__link {
  color: white;
  transition: 2s;
}
.contacts__link:hover {
  color: yellow;
  transition: 1s;
}
@media (max-width: 992px) {
  .contacts__list {
    display: flex;
    justify-content: center;
  }
  .contacts__item {
    margin-left: 20px;
    margin-right: 20px;
  }
}
